.footer {
  position: relative;
  padding: 0px 20px 140px 20px;
  background-color: #0b1315;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 104px minmax(334px, 800px) 100px;
      grid-template-columns: 104px minmax(334px, 800px) 100px;
  gap: 4vw;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #eee;
}

.footer img {
  width: 104px;
  content: url("../images/LogoRed.png");
}

.footer .restaurant {
  text-align: left;
}

.footer .restaurant .name {
  font-size: 44px;
  padding-bottom: 0px;
}

.footer .buttons {
  width: 100%;
  height: 100%;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
}

.footer .buttons a {
  font-size: 14px;
}

.footer .buttons a:hover {
  cursor: pointer;
  color: #c9ab81;
}

.footer .copy {
  position: absolute;
  width: 100%;
  bottom: 20px;
  margin: auto;
}

@media (max-width: 602px) {
  .footer {
    -ms-grid-columns: minmax(104px, 1fr) 1fr;
        grid-template-columns: minmax(104px, 1fr) 1fr;
    -ms-grid-rows: 1fr auto;
        grid-template-rows: 1fr auto;
  }
  .footer img {
    margin-left: 20px;
  }
  .footer .buttons {
    -ms-grid-column: 2;
    -ms-grid-column-span: 1;
    grid-column: 2/3;
    -ms-grid-row: 1;
    -ms-grid-row-span: 1;
    grid-row: 1/2;
    margin: auto;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    padding-right: 30px;
  }
  .footer .restaurant {
    -ms-grid-column: 1;
    -ms-grid-column-span: 2;
    grid-column: 1/3;
    text-align: center;
  }
}
